$d: 70;      // density
$w: 600;     // grid size
$s: 4s;      // speed
$xmas-sky-color:#153d54;
// Generate the snow image using radial gradients
$grad: ();
@for $i from 0 to $d {
  $v: random(3) + 2; // size
  $a: random(5) * .1 + .5;
  $grad: $grad, radial-gradient(
      $v+px $v+px at
      (random($w - $v * 2) + $v)+px
      (random($w - $v * 2) + $v)+px, 
      rgba(255,255,255,$a) 70%,
      rgba(0,0,0,0)
  ) !global;
}
.window{
    width:100%;
    height:100%;
.xmas-sky {
    background: linear-gradient(0deg, $xmas-sky-color, darken($xmas-sky-color, 15%)); ;
    width: 100%;
    height: 100%;
    position: absolute;
}
}

// Snow CSS by by @KeithClark
.snow, .snow:before, .snow:after {
    position: absolute;
    top: -$w + px;
    left: 0;
    bottom: 0;
    right: 0;
    background-image: $grad;
    background-size: $w + px $w + px;
    animation: snow $s linear infinite;
    content: "";
  }
  
  .snow:after {
    margin-left: -$w/3 + px;
    opacity: .4;
    animation-duration: $s*2;
    animation-direction: reverse;
    filter: blur(3px);
  }
  
  .snow:before {
    animation-duration: $s*3;
    animation-direction: reverse;
    margin-left: -$w/2 + px;
    opacity: .65;
    filter: blur(1.5px);
  }
  
  @keyframes snow {
    to {
      transform: translateY($w + px);
    }
  }
